var that;
export default {
	data() {
		return {
			gid: '',
			info: '',
			data: {},
			dzpay: false,
			payaction: -1,
			content: '',
			dzcard: [],
			modal: false,
			refund:['不想要','拍多','货物损坏'],
			index:0
		};
	},
	methods: {
		prinCat(e){
			 that.index=e.detail.value;
		},
		getinfo() {
			this.$request
				.request({
					url: that.api.order.refund_preview,
					data: {
						order_detail_id: that.gid
					}
				})
				.then(res => {
					uni.hideLoading();
					that.info = res;
				});
		},
		sub(){
			uni.showLoading({
				mask:true,
				title:'正在提交'
			})
			this.$request
			.request({
				url: that.api.order.refund,
				data: {
					order_detail_id: that.gid,
					type: 1,
					desc: that.refund[that.index],
					pic_list: []
				},
				method:'post'
			})
			.then(res => {
				console.log(res);
				that.$alertok("退款申请已提交")
				uni.navigateBack({
					delta:1
				})
			}); 
			uni.hideLoading()
		}
	},
	onLoad(option) {
		console.log(option);
		that = this;
		that.gid = option.id ? option.id : '24';
		that.getinfo();
		that.userinfo = uni.getStorageSync('userinfo');
	}
};
